<template>
 <div>
  <!-- 面包屑导航 -->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    <el-breadcrumb-item>用户列表</el-breadcrumb-item>
  </el-breadcrumb>
  <!-- 卡片 -->
  <el-card class="box-card">
      <!-- 行 -->
    <el-row :gutter="20">
        <!-- 列 -->
        <el-col :span="8">
          <el-input placeholder="请输入内容"  class="input-with-select" v-model="queryInfo.query" clearable @clear="getUsers">
           <el-button slot="append" icon="el-icon-search" @click="getUsers"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
            <el-button type="primary" @click="adduser">添加用户</el-button>
        </el-col>
    </el-row>
    <!-- 表格 -->
    <el-table border style="width: 100%" :data="userList" stripe>
        <el-table-column type=index></el-table-column>
        <el-table-column prop="username" label="姓名"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="mobile" label="电话"></el-table-column>
        <el-table-column prop="role_name" label="角色"></el-table-column>
        <el-table-column label="状态">
            <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
            </el-switch>
            </template>
        </el-table-column>
        <el-table-column label="操作" width="180px">
            <template slot-scope="scope">
                <!-- 修改按钮 -->
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="modifyuser(scope.row.id)"></el-button>
                <!-- 删除 -->
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteUser(scope.row.id)"></el-button>
                <!-- 修改角色 -->
                <el-tooltip class="item" effect="dark" content="修改角色" placement="top" :enterable="false">
                 <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
                </el-tooltip>
            </template>
        </el-table-column>
    </el-table>
    <!-- 分页功能 -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
  <!-- 添加用户功能 -->
  <!-- 顶部区域 -->
        <el-dialog
         title="添加用户"
         :visible.sync="addUserVisible"
         width="30%" @close="addDialongClosed">
         <!-- 内容区域 -->
        <el-form :model="addForm" :rules="userRules" ref="addFormRef" class="demo-ruleForm" label-width="60px">
          <el-form-item label="姓名" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="手机" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
         <!-- 底部区域 -->
         <span slot="footer" class="dialog-footer">
           <el-button @click="addUserVisible = false">取 消</el-button>
           <el-button type="primary" @click="addUserForm">确 定</el-button>
         </span>
        </el-dialog>
        <!-- 修改用户功能 -->
        <!-- 顶部区域 -->
              <el-dialog
               title="修改用户"
               :visible.sync="modifyUserVisible"
               width="30%" @close="modifyDialongClosed">
               <!-- 内容区域 -->
              <el-form :model="modifyForm" :rules="userRules" ref="modifyFormRef" class="demo-ruleForm" label-width="70px">
                <el-form-item label="姓名">
                  <el-input v-model="modifyForm.username" disabled></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                  <el-input v-model="modifyForm.email"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="mobile">
                  <el-input v-model="modifyForm.mobile"></el-input>
                </el-form-item>
              </el-form>
               <!-- 底部区域 -->
               <span slot="footer" class="dialog-footer">
                 <el-button @click="modifyUserVisible = false">取 消</el-button>
                 <el-button type="primary" @click="modifyUserForm">确 定</el-button>
               </span>
              </el-dialog>
              <!-- 修改角色 -->
            <el-dialog
              title="修改角色"
              :visible.sync="roledialogVisible"
              width="50%"
              @close="setRoleClosed"
              >
              <p>当前的用户:{{setRoleList.username}}</p>
              <p>当前的角色:{{setRoleList.role_name}}</p>
              <p><span>分配新角色：</span>
                <el-select v-model="selectedRoleId" placeholder="请选择">
                  <el-option
                    v-for="item in roleList"
                    :key="item.id"
                    :label="item.roleName"
                    :value="item.id">
                  </el-option>
                </el-select>
              </p>
              <span slot="footer" class="dialog-footer">
                <el-button @click="roledialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="modifyRole">确 定</el-button>
              </span>
            </el-dialog>
 </div>
</template>

<script>
export default {
  created() {
    this.getUsers()
  },
  data() {
    var checkEmail = (rule, value, callback) => {
      const reg = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
      if (reg.test(value)) {
        callback()
      } else { callback(new Error('邮箱格式不正确')) }
    }
    var checkMobile = (rule, value, cb) => {
      const reg = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (reg.test(value)) {
        cb()
      } else { cb(new Error('手机号码格式不正确')) }
    }
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      userList: [],
      total: 0,
      addUserVisible: false,
      // 修改用户表单状态
      modifyUserVisible: false,
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 修改用户数据
      modifyForm: {},
      userRules: {
        username: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 角色修改对话框显示与隐藏
      roledialogVisible: false,
      // 角色修改用到的用户数据
      setRoleList: [],
      // 角色信息列表
      roleList: [],
      // 选中的角色id
      selectedRoleId: []
    }
  },
  methods: {
    async getUsers() {
      const { data: res } = await this.$http.get('users', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$message.error('获取用户列表失败')
      }
      this.userList = res.data.users
      this.total = res.data.total
    }, // 每页显示个数
    handleSizeChange: function(pageSize) {
      this.queryInfo.pagesize = pageSize
      this.getUsers()
    }, // 页面的改变
    handleCurrentChange(page) {
      this.queryInfo.pagenum = page
      this.getUsers()
    }, // 状态
    async userStateChanged(user) {
      const { data: res } = await this.$http.put(`users/${user.id}/state/${user.mg_state}`)
      if (res.meta.status !== 200) {
        return this.$message.error('更新用户状态失败！')
      }
      this.$message.success('更新用户状态成功')
    },
    handleClose(done) {
      console.log(done)
    },
    adduser() {
      this.addUserVisible = true
    },
    // 获取修改用户
    async modifyuser(uid) {
      const { data: res } = await this.$http.get('users/' + uid)
      this.modifyForm = res.data
      this.modifyUserVisible = true
    },
    // 监听添加用户对话框的关闭事件,关闭就清空
    addDialongClosed() {
      this.$refs.addFormRef.resetFields()
    },
    modifyDialongClosed() {
      this.$refs.modifyFormRef.resetFields()
    },
    addUserForm() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message.error('添加失败！')
        }
        this.$message.success('用户添加成功！')
        // 关闭添加框
        this.addUserVisible = false
        // 重新获取页面
        this.getUsers()
      })
    },
    modifyUserForm() {
      // 表单预验证
      this.$refs.modifyFormRef.validate(async valid => {
        if (!valid) return
        // 开始请求服务器
        const { data: res } = await this.$http.put('users/' + this.modifyForm.id, {
          email: this.modifyForm.email,
          mobile: this.modifyForm.mobile
        })
        if (res.meta.status !== 200) {
          return this.$message.error('用户修改失败')
        }
        // 提示成功
        this.$message.success('用户修改成功')
        // 关闭添加框
        this.modifyUserVisible = false
        // 重新获取页面进行渲染
        this.getUsers()
      })
    },
    // 删除用户
    async deleteUser(uid) {
      const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(() => {
        this.$message.info('已取消删除')
      })
      /* 点确定删除返回confirm
      console.log(confirmResult) */
      if (confirmResult !== 'confirm') {
        return false
      }
      const { data: res } = await this.$http.delete('users/' + uid)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败!')
      }
      this.$message.success('用户删除成功!')
      // 从新请求页面
      this.getUsers()
    },
    // 渲染角色信息
    async setRole(userinfo) {
      this.setRoleList = userinfo
      // 获取角色列表
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.info('获取角色列表失败!')
      }
      this.roleList = res.data
      this.roledialogVisible = true
    },
    // 修改角色信息
    async modifyRole() {
      const { data: res } = await this.$http.put(`users/${this.setRoleList.id}/role`, {
        rid: this.selectedRoleId
      })
      if (res.meta.status !== 200) {
        return this.$message.info('修改角色失败!')
      }
      this.$message.success('修改角色成功!')
      this.getUsers()
      this.roledialogVisible = false
    },
    // 修改角色框关闭
    setRoleClosed () {
      this.selectedRoleId = ''
      this.setRoleList = ''
    }
  }
}
</script>

<style lang="less" scoped>
.el-breadcrumb {
    margin-bottom: 20px;
}
.el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15 );
}
.el-table {
    margin-top: 10px;
}
.el-pagination {
    margin-top: 15px;
}
</style>
