<template>
<div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>角色管理</el-breadcrumb-item>
        <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
        <el-button type="primary" @click="openAddRole">添加角色</el-button>
        <!-- 表格区 -->
        <el-table :data="roleList" border stripe>
            <!-- 展开项 -->
            <el-table-column type="expand">
                <template slot-scope="scope">
                  <el-row v-for="(item1,index) in scope.row.children" :key="item1.id" :class="['bdbottom', index === 0 ? 'bdtop' : '', 'vcenter']">
                      <!-- 一级权限 -->
                    <el-col :span="5">
                        <el-tag closable @close="removeTag(scope.row,item1.id)">{{item1.authName}}</el-tag>
                        <i class="el-icon-caret-right"></i>
                    </el-col>
                    <!-- 二级和三级 -->
                    <el-col :span="19">
                        <el-row v-for="(item2,index2 ) in item1.children" :key="item2.id" :class="[index2 === 0 ? '' : 'bdtop', 'vcenter']" >
                             <!-- 二级权限 -->
                            <el-col :span="6"><el-tag type="success" closable @close="removeTag(scope.row,item2.id)">{{item2.authName}}</el-tag><i class="el-icon-caret-right"></i></el-col>
                             <!-- 三级权限 -->
                            <el-col :span="18"><el-tag type="warning" v-for="(item3) in item2.children" :key="item3.id" closable @close="removeTag(scope.row,item3.id)">{{item3.authName}}</el-tag></el-col>
                        </el-row>
                    </el-col>
                  </el-row>
                </template>
            </el-table-column>
            <!-- index -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column prop="roleName" label="角色名称"> </el-table-column>
            <el-table-column prop="roleDesc" label="角色描述"> </el-table-column>
            <el-table-column label="操作" width="300px">
                <template slot-scope="scope">
                    <!-- 修改按钮 -->
                    <el-button type="primary" icon="el-icon-edit" size="mini" @click="getIDRole(scope.row)">编辑</el-button>
                    <!-- 删除 -->
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteRole(scope.row.id)">删除</el-button>
                    <!-- 分配权限 -->
                    <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRightDialogVisible(scope.row)">分配权限</el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-card>
          <!-- 分配权限对话框 -->
          <el-dialog
              title="分配权限"
              :visible.sync="rightDialogVisible"
              width="50%"
              @close="clearCheckList"
              >
              <!-- 树形控件 -->
              <el-tree :data="rightList" :props="rightProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defaultCheck" ref="treeRef"></el-tree>
              <span slot="footer" class="dialog-footer">
                <el-button @click="rightDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="allotRights">确 定</el-button>
              </span>
          </el-dialog>
          <!-- 编辑角色 -->
          <el-dialog
            title="修改角色"
            :visible.sync="modifyDialogVisible"
            width="30%"
            >
             <el-form :model="roleForm" :rules="roleRules" ref="roleFormRef" label-width="80px">
               <el-form-item label="角色名称" prop="roleName">
                  <el-input v-model="roleForm.roleName"></el-input>
               </el-form-item>
               <el-form-item label="角色描述" prop="roleName">
                  <el-input v-model="roleForm.roleDesc"></el-input>
               </el-form-item>
             </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="modifyDialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="modifyRole">确 定</el-button>
            </span>
          </el-dialog>
           <!-- 添加角色 -->
          <el-dialog
            title="添加角色"
            :visible.sync="addDialogVisible"
            width="30%"
             @close="offAddRole"
            >
             <el-form :model="addRoleForm" :rules="roleRules" ref="addRoleFormRef" label-width="80px">
               <el-form-item label="角色名称" prop="roleName">
                  <el-input v-model="addRoleForm.roleName"></el-input>
               </el-form-item>
               <el-form-item label="角色描述" prop="roleName">
                  <el-input v-model="addRoleForm.roleDesc"></el-input>
               </el-form-item>
             </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="addDialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="addRole">确 定</el-button>
            </span>
          </el-dialog>
</div>
</template>

<script>
export default {
  data() {
    return {
      roleList: [],
      rightDialogVisible: false,
      rightList: [],
      rightProps: {
        children: 'children',
        label: 'authName'
      },
      // 默认选中的权限数组
      defaultCheck: [],
      // id
      roleId: '',
      // 修改显示与隐藏
      modifyDialogVisible: false,
      // 根据id查询到的角色
      roleForm: {
        roleId: '',
        roleName: '',
        roleDesc: ''
      },
      // 规则
      roleRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          { min: 1, message: '角色名称不能为空', trigger: 'blur' }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          { min: 1, message: '角色描述不能为空', trigger: 'blur' }
        ]
      },
      // 角色id
      modifyRoleId: '',
      // 添加角色的显示与隐藏
      addDialogVisible: false,
      // 添加角色信息
      addRoleForm: {
        roleName: '',
        roleDesc: ''
      }
    }
  },
  created() {
    this.getRole()
  },
  methods: {
    async getRole() {
      const { data: res } = await this.$http.get('roles')
      this.roleList = res.data
    },
    async removeTag(role, tagId) {
      const check = await this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (check !== 'confirm') {
        return this.$message.info('已经取消成功！')
      }
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${tagId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败！')
      }
      this.$message.success('删除权限成功！')
      // 重新赋值
      role.children = res.data
    },
    // 分配权限按钮
    async setRightDialogVisible(right) {
      // 获取id
      this.roleId = right.id
      // 请求权限列表
      const { data: res } = await this.$http.get('rights/tree')
      this.rightList = res.data
      this.getLeafKeys(right, this.defaultCheck)
      // 显示互动框
      this.rightDialogVisible = true
    },
    // 递归调用筛选选中三级节点ID
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 关闭互动框，清空数组
    clearCheckList() {
      this.defaultCheck = []
    },
    // 确定按钮，进行提交选中的权限
    async allotRights() {
      var ridsList = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      var ridsStr = ridsList.join(',')
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: ridsStr })
      if (res.meta.status !== 200) {
        return this.$message.error('权限授予失败！')
      }
      this.$message.success('权限授予成功！')
      this.getRole()
      // 关闭互动框
      this.rightDialogVisible = false
    },
    // 渲染角色信息
    async getIDRole(role) {
      const { data: res } = await this.$http.get('roles/' + role.id)
      this.roleForm = res.data
      this.modifyRoleId = role.id
      this.modifyDialogVisible = true
    },
    // 提交按钮
    async modifyRole() {
      const { data: res } = await this.$http.put('roles/' + this.modifyRoleId, {
        roleName: this.roleForm.roleName,
        roleDesc: this.roleForm.roleDesc
      })
      if (res.meta.status !== 200) {
        return this.$message.error('角色修改失败！')
      }
      this.$message.success('角色修改成功')
      this.getRole()
      this.modifyDialogVisible = false
    },
    // 删除角色
    async deleteRole(uid) {
      const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(() => {
        this.$message.info('已取消删除')
      })
      /* 点确定删除返回confirm
      console.log(confirmResult) */
      if (confirmResult !== 'confirm') {
        return false
      }
      const { data: res } = await this.$http.delete('roles/' + uid)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败!')
      }
      this.$message.success('用户删除成功!')
      // 从新请求页面
      this.getRole()
    },
    // 添加角色
    openAddRole() {
      this.addDialogVisible = true
    },
    async addRole() {
      const { data: res } = await this.$http.post('roles', this.addRoleForm)
      if (res.meta.status !== 201) {
        return this.$message.error('角色创建失败!')
      }
      this.$message.success('角色创建成功!')
      this.addDialogVisible = false
      // 从新请求页面
      this.getRole()
    },
    offAddRole() {
      this.addRoleForm = {}
    }
  }
}
</script>

<style lang="less" scoped>
.el-breadcrumb {
    margin-bottom: 15px;
}
.el-table {
    margin-top: 10px;
}
.el-tag{
    margin: 7px;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.bdtop {
  border-top: 1px solid #eee;
}
.vcenter {
    display: flex;
    align-items: center;
}
</style>
